import TripFilterView from "./view/trip-filter.js";
import {renderElement, RenderPosition} from "./util/render";
import TripMenuView from "./view/trip-menu.js";
import {getTripEventItem} from "./mock/mock-trip-event-item.js";
import TripBoard from "./presenter/tripBoard";
import TripInfo from "./presenter/tripInfo";

const DATA_COUNT = 15;

const tripItems = new Array(DATA_COUNT).fill().map(getTripEventItem);
// Array  
// DATA_COUNT    ,       
// fill()     ,    underfine
// map(tripEventItem)     map();

const tripMainElement = document.querySelector(`.trip-main`);
const tripControlsElement = tripMainElement.querySelector(`.trip-main__trip-controls`);
const renderMenu = () => {
  const visuallyHiddenFirstH2Element = tripControlsElement.querySelector(`h2.visually-hidden`);
  renderElement(visuallyHiddenFirstH2Element, new TripMenuView(), RenderPosition.AFTEREND); //  
};

const renderFilter = () => {
  renderElement(tripControlsElement, new TripFilterView(), RenderPosition.BEFOREEND); //   
};

const tripEventElement = document.querySelector(`.trip-events`);


renderMenu();
renderFilter();
const tripBoardPresenter = new TripBoard(tripEventElement); //        
const tripInfoPresenter = new TripInfo(tripMainElement); // tripInfoElement
tripInfoPresenter.init(tripItems); //  info
tripBoardPresenter.init(tripItems); //  


