import AbstractView from "./abstract.js";

export default class SmartView extends AbstractView {
  constructor() {
    super();
    this._dataItem = {}; // 
  }

  // 2
  //  updateData,       dataItem
  updateData(update, justDataUpdating) { //    (update),     dataItem  // justDataUpdating
    if (!update) { //   
      return; //    
    }

    this._dataItem = Object.assign( //    
        {}, //  *
        this._dataItem, //    *
        update //   *
    );

    if (justDataUpdating) { //      ,    ( ).       ..     .
      return;
    }
    this.updateElement(); //   
  }

  // 1
  //   updateElement,     DOM ,        
  updateElement() {
    let prevElement = this.getElement(); //  ()  
    const parent = prevElement.parentElement; //     edite
    this.removeElement(); //   Edit    
    const newElement = this.getElement(); //      
    parent.replaceChild(newElement, prevElement); //    Edit  

    this.restoreHandlers(); //       
  }

  //             
  restoreHandlers() {
    throw new Error(`Abstract metod not implemented: resetHandlers`);
  }
}
