import dayjs from "dayjs";
import AbstractView from "../view/abstract";
/* global require */
const duration = require(`dayjs/plugin/duration`);
dayjs.extend(duration); //      

//  0
const addZeroToNumber = (number) => {
  return (number < 10) ? `0${number}` : number;
};

//    
const getDateDiff = (start, finish) => {
  const diffTimeInMs = finish.diff(start);
  const timeDuration = dayjs.duration(diffTimeInMs); //       {years: 0, months: 0, days: 4, hours: 4, minutes: 1, }
  const days = timeDuration.days(); //   
  const hours = timeDuration.hours(); //   
  const minutes = timeDuration.minutes(); //   
  const time = `${(days > 0) ? addZeroToNumber(days) + `D ` : ``}${(hours > 0) ? addZeroToNumber(hours) + `H ` : ``}${(minutes > 0) ? addZeroToNumber(minutes) + `M` : ``}`;
  return time;
};

//      
const RenderPosition = {
  AFTERBEGIN: `afterbegin`,
  BEFOREEND: `beforeend`,
  AFTEREND: `afterEnd`
};


//                   
//      
const renderElement = (container, element, position) => {
  if (container instanceof AbstractView) { //       Abstract
    //  instanceof ,      
    container = container.getElement(); //     ,     getElement
  }
  //   child(element)        getElement();
  if (element instanceof AbstractView) {
    element = element.getElement();
  }

  switch (position) { //  
    case RenderPosition.AFTERBEGIN:
      container.prepend(element);
      break;
    case RenderPosition.BEFOREEND:
      container.append(element);
      break;
    case RenderPosition.AFTEREND:
      container.after(element);
      break;
  }
};

const createElement = (template) => {
  const newElement = document.createElement(`div`); //   div
  newElement.innerHTML = template; //  HTML        div-,   DOM-

  return newElement.firstChild; //    ()    div
  // ..       
};


const remove = (component) => {
  if (!(component instanceof AbstractView)) {
    throw new Error(`Can remove only components`);
  }
  component.getElement().remove();
  component.removeElement();
};


export {getDateDiff, renderElement, createElement, RenderPosition, remove};
