import AbstractView from "./abstract";
import {SortType} from "../const.js";

const createTripEventsSort = () => {
  return `<form class="trip-events__trip-sort  trip-sort" action="#" method="get" >
            <div class="trip-sort__item  trip-sort__item--day">
              <input id="sort-day" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-day" data-sort-type="${SortType.DAY}" checked>
              <label class="trip-sort__btn" for="sort-day">Day</label>
            </div>

            <div class="trip-sort__item  trip-sort__item--event">
              <input id="sort-event" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-event" disabled>
              <label class="trip-sort__btn" for="sort-event">Event</label>
            </div>

            <div class="trip-sort__item  trip-sort__item--time" >
              <input id="sort-time" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-time" data-sort-type="${SortType.TIME}">
              <label class="trip-sort__btn" for="sort-time">Time</label>
            </div>

            <div class="trip-sort__item  trip-sort__item--price">
              <input id="sort-price" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-price" data-sort-type="${SortType.PRICE}">
              <label class="trip-sort__btn" for="sort-price">Price</label>
            </div>

            <div class="trip-sort__item  trip-sort__item--offer">
              <input id="sort-offer" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-offer" disabled>
              <label class="trip-sort__btn" for="sort-offer">Offers</label>
            </div>
          </form>
`;
};


export default class TripEventsSortView extends AbstractView {
  constructor() {
    super();
    this._sortTypeChangeHandler = this._sortTypeChangeHandler.bind(this); // 7 bind 
  }

  getTemplate() {
    return createTripEventsSort(); // this._dataItem
  }

  _sortTypeChangeHandler(evt) { // 4      
    evt.preventDefault();
    //     
    this._callback.sortTypeChange(evt.path[0].dataset.sortType); // 6     A   .SortType
    //     evt.path[0].dataset.sortType    
    // sortType       data-  data-sort-type
    //    
  }

  setSortTypeChangeHandler(callback) {
    this._callback.sortTypeChange = callback; // 1    prezenter tripBoard
    this.getElement().addEventListener(`change`, this._sortTypeChangeHandler);
    // this.getElement()  , 2    
    // .addEventListener(`click`, 3   
    // this._sortTypeChangeHandler)  
  }
}
